color:#7261be !important;

padding:0px !important;

}

.linka:hover{

color:#7261be !important;

padding:0px !important;

}

.errorMsg {

color: #cc0000;

margin-bottom: 12px;

}

.sucessMsg {

color: #6B8E23;

margin-bottom: 10px;

}

